@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/opensans-light-webfont.woff2") format("woff2"), url("../fonts/opensans-light-webfont.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans-regular-webfont.woff2") format("woff2"), url("../fonts/opensans-regular-webfont.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/opensans-bold-webfont.woff2") format("woff2"), url("../fonts/opensans-bold-webfont.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensans-extrabold-webfont.woff2") format("woff2"), url("../fonts/opensans-extrabold-webfont.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 28px;
  font-weight: 300;
  background-color: #e5e5e5; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.page-header {
  background-color: rgba(0, 0, 0, 0.8); }

.page-header__menu a {
  cursor: pointer; }

.page-header__logo-image:hover path,
.page-header__logo-image:focus path {
  fill: #ffffff; }

.page-header__logo-image:active path {
  fill: rgba(10, 217, 198, 0.3); }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.main-nav__list-item a {
  line-height: 32px;
  color: #ffffff;
  opacity: 0.7;
  text-align: center;
  cursor: pointer;
  outline: none; }
  .main-nav__list-item a:hover, .main-nav__list-item a:focus {
    color: #0ad9c6;
    opacity: 1; }
  .main-nav__list-item a:active {
    opacity: 0.3; }

.information__title {
  font-size: 48px;
  line-height: 64px;
  font-weight: 700;
  color: #ffffff; }

.information__description {
  font-size: 20px;
  line-height: 32px;
  color: #ffffff;
  opacity: 0.8; }

.form__title {
  font-size: 17px;
  line-height: 28px;
  font-weight: 600;
  color: #ffffff;
  text-align: center; }

.form input::placeholder {
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: #1c1c1c;
  opacity: 0.5;
  outline: none; }

.form input:hover, .form input:focus, .form input:active {
  border: 2px solid #0ad9c6;
  outline: none; }

.form button {
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  color: #000000;
  text-align: center;
  background-color: #0ad9c6;
  border: none; }
  .form button:hover, .form button:focus {
    opacity: 0.7;
    outline: none; }
  .form button:active {
    opacity: 0.3; }

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.advantages__item {
  font-size: 22px;
  line-height: 34px;
  font-weight: 400;
  color: #000000;
  text-align: center; }

.title {
  font-size: 30px;
  line-height: 30px;
  font-weight: 600;
  text-align: center;
  color: #1f1f1f; }

.kinds__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.kinds__item {
  font-size: 22px;
  line-height: 34px;
  font-weight: 600;
  color: #000000;
  text-align: center; }

.video {
  background-color: #131314; }
  .video button {
    width: 65px;
    height: 65px;
    background-color: #0ad9c6;
    border-radius: 50%;
    outline: none;
    cursor: pointer;
    border: none; }
    .video button:hover, .video button:focus {
      opacity: 0.7; }
    .video button:active {
      opacity: 0.3; }

.video__title {
  color: #ffffff; }

.video__description {
  text-align: center;
  color: #ffffff;
  opacity: 0.7; }

.contacts address {
  font-size: 17px;
  line-height: 31px;
  font-weight: 400;
  font-style: normal;
  text-align: center;
  color: rgba(0, 0, 0, 0.7); }

.page-footer__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.page-footer__item a {
  line-height: 22px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
  outline: none; }
  .page-footer__item a:hover, .page-footer__item a:focus {
    color: #0ad9c6; }
  .page-footer__item a:active {
    opacity: 0.7; }
